





























import Vue from "vue";
import Expand from "@/components/Expand.vue";
export default Vue.extend({
  name: "Navbar",
  components: {
    Expand
  },
  data() {
    return {
      expanded: false,
      title: "",
      submit: "Submit",
      cancel: "Cancel"
    };
  },
  computed: {
    buttonText() {
      if (this.title) return "Ok";
      return "Cancel";
    }
  },
  methods: {
    onExpand(): void {
      this.expanded = true;
      this.$nextTick(() => {
        // @ts-ignore
        this.$refs.title.focus();
      });
    },
    addList(): void {
      if (this.title) {
        this.$store.commit("addList", { title: this.title });
      }
      this.title = "";
      this.expanded = false;
    }
  }
});
